'use strict';

module.exports = function onClose(self) {
  return function () {
    self.l.debug(function () {
      return ['disconnected from', self.endpoint];
    });

    self.isConnected = false;
    self.emitter.emit('disconnected');

    if (self.autoConnect) {
      setTimeout(function () {
        // FIXME: This is a circular dependency
        require('./connect')(self);
      }, 1000);
    }
  };
}; // Copyright 2017-2018 Jaco Greeff
