module.exports = function onMessageSubscribe(self, response) {
  self.l.debug(function () {
    return ['handling: response =', response, 'subscription =', response.params.subscription];
  });

  var handler = self.subscriptions[response.params.subscription];

  if (!handler) {
    self.l.error('Unable to find handler for subscription=' + response.params.subscription);
    return;
  }

  try {
    var result = self.coder.decodeResponse(response);

    handler.callback(null, result);
  } catch (error) {
    handler.callback(error);
  }
};


